#lists-space {
  padding: 0 0.5rem;
  gap: 0.5rem;
  height: 90vh;
  display: flex;
  align-items: flex-start;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
}

.list-con {
  max-width: 300px;
  min-width: 300px;
  max-height: 90%;
  background-color: #678fc2;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  padding-bottom: 0.5rem;
}
.list-con .add-toggle {
  width: auto;
  margin: 0 0.5rem;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.list-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 50px;
  margin-bottom: 0.5rem;
  padding: 0.5rem;
}

.list-options {
  position: relative;
}

.list-option-btn {
  color: black;
  padding: 0.25rem;
  border-radius: 2.5px;
  border: none;
  background-color: transparent;
}
.list-option-btn:hover {
  background-color: rgba(0, 0, 0, 0.25);
  transition-duration: 250ms;
  cursor: pointer;
}

.list-scroll {
  overflow-y: auto;
  width: 100%;
  padding-right: 0.25rem;
}
.list-scroll *:last-child {
  margin-bottom: 10px;
}

.add-con {
  width: 300px;
  background-color: #678fc2;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 0.5rem;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.add-input {
  width: 100%;
  height: 30px;
  font-weight: bold;
  align-self: center;
  border-radius: 5px;
  padding: 5px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-style: none;
}
.input-card {
  font-size: large;
  height: 60px;
  text-align: left;
  resize: none;
}
.input-title {
  font-size: large;
}

.add-buttons {
  margin-top: 10px;
  display: flex;
  align-items: center;
}

.add-button {
  color: white;
  background-color: rgb(45, 108, 243);
  border-radius: 5px;
  padding: 0.25rem;
  margin-right: 20px;
}
.add-button:hover {
  background-color: rgb(31, 74, 167);
  cursor: pointer;
  transition-duration: 250ms;
}

.add-cancel {
  background-color: transparent;
  padding: 0.25rem 0.5rem;
  border: none;
}
.add-cancel:hover {
  cursor: pointer;
}
.add-cancel * {
  color: rgba(0, 0, 0, 0.75);
}
.add-cancel:hover * {
  color: rgba(0, 0, 0, 1);
  transition-duration: 250ms;
}

.add-toggle {
  width: 300px;
  position: static;
  color: black;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 0.5rem;
  border: none;
  display: inline-block;
  text-align: left;
}
.add-toggle:hover {
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition-duration: 250ms;
}
.add-toggle * {
  display: inline-block;
}
.add-toggle *:last-child {
  margin-left: 10px;
}
.fixed-width {
  min-width: 300px;
}

.list-card {
  background-color: white;
  color: #282c34;
  font-weight: bold;
  padding: 0.5rem;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  word-break: break-all;
  margin: 0 0.5rem;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.list-card:hover {
  cursor: pointer;
  background-color: rgb(209, 209, 209);
}
.list-card:not(:last-of-type) {
  margin-bottom: 0.5rem;
}
.list-card *:last-child:hover {
  background-color: #777777;
  transition-duration: 250ms;
}

.current {
  background-color: #416ea7;
}

.add-list-button {
  color: black;
  max-width: 300px;
  min-width: 300px;
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  padding: 0.5rem;
  align-items: center;
  border: none;
}
.add-list-button:hover {
  background-color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition-duration: 250ms;
}
.add-list-button *:last-child {
  margin-left: 10px;
}

.add-list-input {
  padding: 0 0.5rem 0.5rem 0.5rem;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
